.screen-left .team-score {
  display: flex;
}
.screen-left .team-score .team-score-left {
  flex: 1;
}
.screen-left .team-task {
  background: #2a273b;
  width: 100%;
  padding: 19px;
}
.screen-left .team-task .team-task-right {
  color: rgba(255, 255, 255, 0.8);
}
.screen-left .team-task .team-task-right > div {
  background: #302e45;
  width: 280px;
  padding: 0 20px;
}
.screen-left .team-task .team-task-right > div h3 {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.screen-left .team-task .team-task-right > div h3 span {
  margin: 0 5px;
}
.screen-left .team-task .team-task-right > div:nth-of-type(1) {
  height: 36%;
  min-height: 36%;
  margin-bottom: 20px;
}
.screen-left .team-task .team-task-right > div:nth-of-type(2) {
  height: 61%;
}
.screen-left .team-task .team-task-left {
  position: relative;
  height: 100%;
  padding-left: 51px;
}
.screen-left .team-task .team-task-left > p {
  font-size: 16px;
  color: #fff;
  line-height: 16px;
  border-left: 2px solid #fff;
  padding-left: 8px;
}
.screen-left .team-task .team-task-left > div {
  min-width: 771px;
}
.screen-left .team-task .team-task-left .leftList {
  width: 330px;
  height: 100px;
  background: #302e45;
  box-shadow: 0px 2px 10px 0px #232031;
  margin: 15px 20px 15px 0;
}
.screen-left .team-task .team-task-left .leftList .ant-col {
  text-align: center;
  padding: 20px 0;
  font-size: 12px;
}
.screen-left .team-task .team-task-left .leftList .ant-col img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.screen-left .team-task .team-task-left .leftList .ant-col h4 {
  color: rgba(255, 255, 255, 0.8);
  text-align: left;
  margin: 7px 0;
}
.screen-left .team-task .team-task-left .leftList .ant-col p {
  color: #fff;
  text-align: left;
}
.screen-left .team-task .team-task-left .leftList .ant-col:nth-of-type(3) {
  color: rgba(255, 255, 255, 0.8);
  border-left: 1px solid rgba(0, 0, 0, 0.2);
  height: 60px;
  margin-top: 20px;
}
.screen-left .team-score .team-score-left .set-style {
  width: 330px !important;
  height: 100px;
  margin: 20px 27px 0 0;
}
.group-details .ant-col {
  margin: 10px 25px 0 0;
}
.group-details .ant-col p {
  text-align: center;
}
.group-details .ant-col:nth-child(6n) {
  margin-right: 0;
}
.group-details > p {
  font-weight: bold;
  font-size: 16px;
}
.group-details img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-bottom: 9px;
  border: 1px solid #eee;
}
.group-details .ant-btn {
  width: 220px;
  height: 44px;
  background: #27cda2;
  border-radius: 22px;
  color: #fff;
  margin: 10px 0 10px 150px;
}
.change-score span {
  font-size: 16px;
}
.group-details .activity_blue {
  background: #3A89FD;
  color: white;
